<template charset>
  <div class="fixed-screen bg-custom-dark text-custom-text font-sans flex flex-col" @contextmenu.prevent="handleContextMenu">
    <header class="w-full bg-custom-gray p-4 flex items-center justify-between">
      <div class="flex items-center">
        <img :class="{'rotate-animation': isInferencing}" src="@/assets/sakura-logo.png" alt="Logo" class="h-12 mr-4">
        <h1 :class="{'breathing-animation': isInferencing}" class="text-2xl font-bold">AIMaster.vip</h1>
      </div>
      <div class="flex items-center space-x-4">
        <label class="flex items-center">
          <span class="mr-2">{{ language === 'zh' ? 'Zh/En' : 'Zh/En' }}</span>
          <div class="toggle-switch" @click="toggleLanguage">
            <div :class="{'toggle-switch-thumb': true, 'toggle-switch-thumb-on': language === 'zh', 'toggle-switch-thumb-off': language !== 'zh'}"></div>
          </div>
        </label>
      </div>
    </header>
    <main class="flex-grow grid grid-cols-12 gap-4 p-4">
      <aside class="col-span-2 bg-custom-gray p-4 rounded">
        <h2 class="text-lg font-semibold mb-4">{{ language === 'zh' ? '综合设置' : 'General Settings' }}</h2>
        <div class="flex flex-col space-y-8">
          <button @click="changePage('initial')" :class="{'bg-custom-purple': activePage === 'initial', 'bg-custom-gray-light': activePage !== 'initial'}" class="py-4 rounded text-center">{{ language === 'zh' ? '初始参数' : 'Initial Parameters' }}</button>
          <button @click="changePage('basic')" :class="{'bg-custom-purple': activePage === 'basic', 'bg-custom-gray-light': activePage !== 'basic'}" class="py-4 rounded text-center">{{ language === 'zh' ? '基础参数' : 'Basic Parameters' }}</button>
          <button @click="changePage('advanced')" :class="{'bg-custom-purple': activePage === 'advanced', 'bg-custom-gray-light': activePage !== 'advanced'}" class="py-4 rounded text-center">{{ language === 'zh' ? '高级设置' : 'Advanced Settings' }}</button>
          <button @click="changePage('keySettings')" :class="{'bg-custom-purple': activePage === 'keySettings', 'bg-custom-gray-light': activePage !== 'keySettings'}" class="py-4 rounded text-center">{{ language === 'zh' ? '按键设置' : 'Key Settings' }}</button>
          <button @click="changePage('aimSettings')" :class="{'bg-custom-purple': activePage === 'aimSettings', 'bg-custom-gray-light': activePage !== 'aimSettings'}" class="py-4 rounded text-center">{{ language === 'zh' ? '目标设置' : 'Aim Settings' }}</button>
        </div>
      </aside>
      <section class="col-span-8 bg-custom-gray p-4 rounded">
        <div class="flex items-center justify-between mb-4">
          <img src="@/assets/Valorant.png" alt="valorant" class="h-24 mb-12">
          <div>
            <button
              
              :class="{'bg-custom-green': !isInferencing, 'bg-custom-red': isInferencing}"
              @click="toggleInferencing; sendStartSignal();"
              class="px-4 py-2 rounded text-center text-white"
              style="min-width: 150px;"
            >
              {{ isInferencing ? (language === 'zh' ? '实时推理中' : 'Inferencing') : (language === 'zh' ? '开始推理' : 'Start Inferencing') }}
            </button>
            <button
              @click="saveParameters"
              class="px-4 py-2 ml-4 rounded text-center text-white bg-custom-blue-light"
            >
              {{ language === 'zh' ? '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;保存参数&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;' : 'Save Parameters' }}
            </button>
            <button
              @click="sendExitSignal"
              class="px-4 py-2 ml-4 rounded text-center text-white bg-custom-blue-light"
            >
              {{ language === 'zh' ? '&nbsp;&nbsp;关闭所有进程&nbsp;&nbsp;' : '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Exit all&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;' }}
            </button>
          </div>
        </div>
        <div v-if="activePage === 'initial'">
          <h2 class="text-lg font-semibold mb-4">{{ language === 'zh' ? '初始参数' : 'Initial Parameters' }}</h2>
          <div class="mb-4">
            <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '截图尺寸' : 'Screenshot Size' }}</label>
            <input type="number" v-model.number="initialParams.boxSize" @input="validateAdvancedParam('initialParams.boxSize', minBoxSize, maxBoxSize)" :min="minBoxSize" :max="maxBoxSize" step="1" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled="isInferencing">
          </div>
          <div class="mb-4">
            <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '模型位置' : 'Model Path' }}</label>
            <input type="text" v-model="initialParams.modelPath" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled="isInferencing">
          </div>
          <div class="mb-4">
            <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '鼠标驱动' : 'Mouse Driver' }}</label>
            <select v-model="mouseDriver" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled="isInferencing">
              <option value="win32api">{{ language === 'zh' ? 'windows模拟驱动' : 'windows Win32Api' }}</option>
              <option value="logitech-ghub">{{ language === 'zh' ? '罗技Ghub驱动' : 'Logitech Ghub driver' }}</option>
              <option value="kmbox-net">{{ language === 'zh' ? 'KMNET硬件' : 'KMNET(hardware)' }}</option>
              <option value="kernel-driver">{{ language === 'zh' ? '内核驱动' : 'Kernel Driver' }} </option>
            </select>
          </div>
          
          <div v-if="mouseDriver === 'kmbox-net'">
            <div class="mb-4">
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'IP' : 'IP' }}</label>
              <input type="text" v-model="kmboxNetParams.ip" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled="isInferencing">
            </div>
            <div class="mb-4">
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '端口' : 'Port' }}</label>
              <input type="text" v-model="kmboxNetParams.port" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled="isInferencing">
            </div>
            <div class="mb-4">
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'UUID' : 'UUID' }}</label>
              <input type="text" v-model="kmboxNetParams.uuid" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4 " :disabled="isInferencing">
            </div>
          </div>
        </div>
        <div v-if="activePage === 'basic'">
          <h2 class="text-lg font-semibold mb-4">{{ language === 'zh' ? '基础参数' : 'Basic Parameters' }}</h2>
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '瞄准速度(kpx)' : 'Aim Speed(kpx)' }}</label>
              <div class="flex items-center">
                <input type="range" v-model.number="advancedParams.kpx" :min="minKpx" :max="maxKpx" step="0.01" class="w-full">
                <span class="ml-2 w-12 text-right">{{ advancedParams.kpx }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '瞄准范围' : 'Aim Range' }}</label>
              <div class="flex items-center">
                <input type="range" v-model.number="basicParams.aimRange" :min="minAimRange" :max="maxAimRange" step="1" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.aimRange }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '置信度' : 'Confidence' }}</label>
              <div class="flex items-center">
                <input type="range" v-model.number="basicParams.confidence" :min="minConfidence" :max="maxConfidence" step="0.01" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.confidence }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '主动睡眠时间' : 'Sleep Time (ms)' }}</label>
              <div class="flex items-center">
                <input type="range" v-model.number="basicParams.sleepMs" :min="minSleepMs" :max="maxSleepMs" step="1" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.sleepMs }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '分段平滑' : 'Segment Smoothing' }}</label>
              <div class="flex items-center">
                <input type="range" v-model.number="basicParams.segmentSmoothing" :min="minSegmentSmoothing" :max="maxSegmentSmoothing" step="1" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.segmentSmoothing }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '扳机间隔' : 'Trigger Interval' }}</label>
              <div class="flex items-center">
                <input type="range" v-model.number="basicParams.triggerInterval" :min="minTriggerInterval" :max="maxTriggerInterval" step="10" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.triggerInterval }}</span>
              </div>
            </div>
            <div class="col-span-2 flex items-center">
              <input type="checkbox" v-model.number="basicParams.debug" class="ml-4 mr-2" style="width: 15px; height: 15px;">{{ language === 'zh' ? '调试模式' : 'Debug Mode' }}
              <input type="checkbox" v-model.number="basicParams.trigger" class="ml-4 mr-2" style="width: 15px; height: 15px;">{{ language === 'zh' ? '扳机' : 'Trigger' }}
              <input type="checkbox" v-model="basicParams.enableLinear" class="ml-4 mr-2" style="width: 15px; height: 15px;">{{ language === 'zh' ? '圆周线性算法\n(需要高级设置/自动测量)' : 'Circumference linear algorithm\n(adv parameters/auto test required)' }}
            </div>
          </div>
        </div>
        <div v-if="activePage === 'advanced'">
          <h2 class="text-lg font-semibold mb-4">{{ language === 'zh' ? '高级设置' : 'Advanced Settings' }}</h2>
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'FOV' : 'FOV' }}</label>
              <input type="number" v-model.number="advancedParams.fov" :min="minFov" :max="maxFov" step="1" @input="validateAdvancedParam('advancedParams.fov', minFov, maxFov)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'SENS' : 'Sensitivity' }}</label>
              <input type="number" v-model.number="advancedParams.sens" :min="minSens" :max="maxSens" step="0.01" @input="validateAdvancedParam('advancedParams.sens', minSens, maxSens)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'ROUNDX' : 'Round X' }}</label>
              <input type="number" v-model.number="advancedParams.roundX" :min="minRoundX" :max="maxRoundX" step="1" @input="validateAdvancedParam('advancedParams.roundX', minRoundX, maxRoundX)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'ROUNDY' : 'Round Y' }}</label>
              <input type="number" v-model.number="advancedParams.roundY" :min="minRoundY" :max="maxRoundY" step="1" @input="validateAdvancedParam('advancedParams.roundY', minRoundY, maxRoundY)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
          </div>
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'Kpx' : 'Kpx' }}</label>
              <input type="number" v-model.number="advancedParams.kpx" :min="minKpx" :max="maxKpx" step="0.01" @input="validateAdvancedParam('advancedParams.kpx', minKpx, maxKpx)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'Kpy' : 'Kpy' }}</label>
              <input type="number" v-model.number="advancedParams.kpy" :min="minKpy" :max="maxKpy" step="0.01" @input="validateAdvancedParam('advancedParams.kpy', minKpy, maxKpx)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'Kix' : 'Kix' }}</label>
              <input type="number" v-model.number="advancedParams.kix" :min="minKix" :max="maxKix" step="0.01" @input="validateAdvancedParam('advancedParams.kix', minKix, maxKix)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'Kiy' : 'Kiy' }}</label>
              <input type="number" v-model.number="advancedParams.kiy" :min="minKiy" :max="maxKiy" step="0.01" @input="validateAdvancedParam('advancedParams.kiy', minKiy, maxKiy)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'Kdx' : 'Kdx' }}</label>
              <input type="number" v-model.number="advancedParams.kdx" :min="minKdx" :max="maxKdx" step="0.01" @input="validateAdvancedParam('advancedParams.kdx', minKdx, maxKdx)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? 'Kdy' : 'Kdy' }}</label>
              <input type="number" v-model.number="advancedParams.kdy" :min="minKdy" :max="maxKdy" step="0.01" @input="validateAdvancedParam('advancedParams.kdy', minKdy, maxKdy)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            
              <div>
                <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '扳机水平系数' : 'Trigger Horizontal factor' }}</label>
                <input type="number" v-model.number="advancedParams.TriggerX" :min="minTriggerX" :max="maxTriggerX" step="0.1" @input="validateAdvancedParam('advancedParams.TriggerX', minTriggerX, maxTriggerX)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled=!basicParams.trigger>
              </div>

              <div>
                <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '扳机垂直系数' : 'Trigger Vertical factor' }}</label>
                <input type="number" v-model.number="advancedParams.TriggerY" :min="minTriggerY" :max="maxTriggerY" step="0.1" @input="validateAdvancedParam('advancedParams.TriggerY', minTriggerY, maxTriggerY)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" :disabled=!basicParams.trigger>
              </div>
            
          </div>
        </div>
        <div v-if="activePage === 'keySettings'" @contextmenu.prevent>
          <h2 class="text-lg font-semibold mb-4">{{ language === 'zh' ? '按键设置' : 'Key Settings' }}</h2>
          <div v-if="conflictMessage" class="text-red-500 font-bold mb-4">{{ conflictMessage }}</div>
          
          <div class="grid grid-cols-2 gap-4 mb-4" @keydown="handleKeydown" @click="handleClickOutside" @contextmenu.prevent="handleContextMenu">
            <div>
              
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '自瞄按键(支持 鼠标/键盘 按键)' : 'Aim Key (supports mouse/keyboard buttons)' }}</label>
              <input type="text" :value="formattedKeySettings.forward" @mousedown="delayedHandleMousedown"  @focus="unlockInput('forward')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4"  @click.stop>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '关闭程序按键' : 'Close Program Key' }}</label>
              <input type="text" :value="formattedKeySettings.backward" @focus="unlockInput('backward')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" @click.stop>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '自动测量参数按键' : 'Auto Test Parameters Key' }}</label>
              <input type="text" :value="formattedKeySettings.left" @focus="unlockInput('left')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" @click.stop>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '类别切换按键' : 'Target Class Switch Key' }}</label>
              <input type="text" :value="formattedKeySettings.right" @focus="unlockInput('right')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" @click.stop>
            </div>
          </div>
        </div>
        <div v-if="activePage === 'aimSettings'">
          <h2 class="text-lg font-semibold mb-4">{{ language === 'zh' ? '瞄准设置' : 'Aim Settings' }}</h2>
          <div class="relative" @mousedown="startDrag" @mousemove="onDrag" @mouseup="endDrag" @mouseleave="endDrag" @contextmenu.prevent>
            <img src="@/assets/human.png" alt="Human" class="w-1/5 mx-auto select-none" draggable="false">
            <div :style="{ top: aimHeight + '%', transform: 'translateY(-50%)' }" class="absolute left-0 w-full h-0.5 bg-red-500"></div>
          </div>
          <div class="mt-4">
            <label class="block text-sm font-medium mb-2">{{ language === 'zh' ? '瞄准高度' : 'Aim Height' }}: {{ aimHeightPercentage }}</label>
          </div>
        </div>
      </section>
      <aside class="col-span-2 bg-custom-gray-dark border border-custom-gray-light rounded">
        <h2 class="text-lg font-semibold mb-4 text-center">
          {{ language === 'zh' ? '调试信息' : 'Debug Information' }}
        </h2>
        <div class="overflow-y-auto" style="height: 80vh"> 
          <div v-for="(message, index) in debugMessages" :key="index" class="text-sm mb-2 text-center">
            {{ message }}
          </div>
        </div>
      </aside>
    </main>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'GameDashboard',
  data() {
    return {
      language: 'zh',
      isInferencing: false,
      activePage: 'initial',
      initialParams: {
        boxSize: 320,
        modelPath: 'model\\valorant_320_2_v8.trt'
      },
      basicParams: {
        aimRange: 100,
        debug: false,
        confidence: 0.3,
        sleepMs: 1,
        trigger: false,
        aimSpeed: 6,
        segmentSmoothing: 15,
        triggerInterval: 500,
        enableLinear: false

      },
      lockedInputs: {
        forward: false,
        backward: false,
        left: false,
        right: false
      },
      advancedParams: {
        fov: 103,
        sens: 0.2,
        roundX: 16355,
        roundY: 9700,
        kpx: 0.4,
        kix: 0.2,
        kdx: 0.00,
        kpy: 0.15,
        kiy: 0,
        kdy: 0,
        TriggerX: 0.5,
        TriggerY: 0.5,
      },
      keySettings: {
        forward: { key: 'L2', vk: '6' },
        backward: { key: 'PAGEUP', vk: '33' },
        left: { key: 'PAGEDOWN', vk: '34' },
        right: { key: 'CAPSLOCK', vk: '20' }
      },
      aimHeight: 50,
      isDragging: false,
      selectedInput: null,
      conflictMessage: '',
      minBoxSize: 0,
      maxBoxSize: 1000,
      minAimRange: 0,
      maxAimRange: 300,
      minAimSpeed: 0,
      maxAimSpeed: 100,
      minConfidence: 0,
      maxConfidence: 1,
      minSleepMs: 0,
      maxSleepMs: 10,
      minFov: 0,
      maxFov: 300,
      minSens: 0,
      maxSens: 10,
      minRoundX: 0,
      maxRoundX: 2000000,
      minRoundY: 0,
      maxRoundY: 2000000,
      minKpx: 0,
      maxKpx: 1,
      minKix: 0,
      maxKix: 1,
      minKdx: 0,
      maxKdx: 1,
      minKpy: 0,
      maxKpy: 1,
      minKiy: 0,
      maxKiy: 1,
      minKdy: 0,
      maxKdy: 1,
      minSegmentSmoothing: 0,
      maxSegmentSmoothing: 30,
      minTriggerInterval: 0,
      maxTriggerInterval: 1000,
      minAimSensitivity: 0,
      maxAimSensitivity: 10,
      maxTriggerX: 1,
      maxTriggerY: 1,
      minTriggerX: 0,
      minTriggerY: 0,
      debugMessages: [],
      mouseDriver: 'win32api',
      kmboxNetParams: {
        ip: '',
        port: '',
        uuid: ''
      },
      receivedData: '' // 新增的用于存储后端返回数据的变量
    };
  },
  mounted() {
    
    
    this.dataFetchInterval = setInterval(this.fetchData, 2000); // 定期获取数据
    window.addEventListener('popstate', this.handlePopState);
    this.loadParameters(); // 加载参数
  },
  beforeUnmount() {
    clearInterval(this.debugInterval);
    clearInterval(this.dataFetchInterval); // 清除定期获取数据的定时器
    window.removeEventListener('popstate', this.handlePopState);
  },
  methods: {
    toggleLinearAlgorithm() {
    if (this.basicParams) {
      console.log('Current value of enableLinear:', this.basicParams.enableLinear);
      this.basicParams.enableLinear = !this.basicParams.enableLinear;
      console.log('New value of enableLinear:', this.basicParams.enableLinear);
    } else {
      console.error('basicParams is undefined');
    }
  },
    toggleLanguage() {
      this.language = this.language === 'zh' ? 'en' : 'zh';
    },
    toggleInferencing() {
      this.isInferencing = !this.isInferencing;
    },
    validateAdvancedParam(param, min, max) {
      if (this[param.split('.')[0]][param.split('.')[1]] < min) {
        this[param.split('.')[0]][param.split('.')[1]] = min;
      } else if (this[param.split('.')[0]][param.split('.')[1]] > max) {
        this[param.split('.')[0]][param.split('.')[1]] = max;
      }
    },
    setAimHeight(event) {
      const rect = event.currentTarget.getBoundingClientRect();
      const y = event.clientY - rect.top;
      const height = rect.height;
      this.aimHeight = Math.min(Math.max((y / height) * 100, 0), 100);
    },
    startDrag(event) {
      this.isDragging = true;
      this.setAimHeight(event);
    },
    onDrag(event) {
      if (this.isDragging) {
        this.setAimHeight(event);
      }
    },
    endDrag() {
      this.isDragging = false;
    },
    unlockInput(inputName) {
      this.lockedInputs[inputName] = false;
      this.selectedInput = inputName;
    },
    handleKeydown(event) {
      if (this.selectedInput) {
        event.preventDefault();
        const key = event.key.toUpperCase();
        const code = event.which;

        const isConflict = Object.keys(this.keySettings).some((input) => {
          return input !== this.selectedInput && this.keySettings[input].key === key;
        });

        if (isConflict) {
          this.conflictMessage = this.language === 'zh' ? '按键绑定不得重复，此次绑定已取消' : 'Key binding must not be duplicated, binding canceled';
        } else {
          this.keySettings[this.selectedInput] = { key, vk: "vk: " + code };
          this.conflictMessage = '';
        }
      }
    },
    delayedHandleMousedown(event) {
      setTimeout(() => {
        this.handleMousedown(event);
      }, 0);
    },
    handleMousedown(event) {
      if (this.selectedInput === "forward") {
        event.preventDefault();
        let key;
        let code;

        switch (event.button) {
          case 0:
            key = this.language === 'zh' ? 'L1' : 'Left Mouse Button';
            code = '1';
            break;
          case 1:
            key = this.language === 'zh' ? 'LM' : 'Middle Mouse Button';
            code = '4';
            break;
          case 2:
            key = this.language === 'zh' ? 'L2' : 'Right Mouse Button';
            code = '2';
            break;
          case 3:
            key = this.language === 'zh' ? 'X2' : 'Mouse Button 4';
            code = '5';
            break;
          case 4:
            key = this.language === 'zh' ? 'X1' : 'Mouse Button 5';
            code = '6';
            break;
          default:
            key = this.language === 'zh' ? 'Unknown?' : 'Unknown';
            code = '-1';
        }

        this.keySettings[this.selectedInput] = { key, vk: code };
      }
    },
    handleClickOutside(event) {
      if (!this.$el.contains(event.target) && this.selectedInput) {
        this.lockedInputs[this.selectedInput] = true;
        this.selectedInput = null;
      }
    },
    handleContextMenu(event) {
      event.preventDefault();
    },
    handlePopState(event) {
      if (event) {
        console.log();
      }
      if (this.activePage === 'keySettings') {
        history.pushState(null, null, document.URL);
      }
    },
    changePage(page) {
      this.activePage = page;
      if (page === 'keySettings') {
        history.pushState(null, null, document.URL);
      }
    },
    async sendStartSignal() {
      // 定义操作是开始还是停止推理
      const action = this.isInferencing ? 'stop' : 'start';

      // 创建数据对象，只包含操作
      const data = { action };

      try {
        // 向后端API发送请求，仅传递操作指令
        const response = await axios.post('/api/control', data);
        console.log('Response:', response.data);

        // 根据响应更新推理状态
        this.isInferencing = !this.isInferencing;

        // 可选择显示成功消息
        alert(this.language === 'zh' ? '推理状态已更改' : 'Inferencing state changed');
      } catch (error) {
        console.error('Error:', error);
        alert(this.language === 'zh' ? '推理状态更改失败' : 'Failed to change inferencing state');
      }
    },

    async sendExitSignal(){
      const action = "exit"
      const exitData = { action };
      
      try {
        // 向后端API发送请求，仅传递操作指令
        const response = await axios.post('/api/control', exitData);
        console.log('Response:', response.exitData);
        console.log('data:', exitData);


        // 可选择显示成功消息
        alert(this.language === 'zh' ? '已关闭' : 'Successfully Quit');
        this.isInferencing = false;
      } catch (error) {
        console.error('Error:', error);
        alert(this.language === 'zh' ? '发送异常，如未结束请尝试手动关闭' : 'Failed to Quit, try to quit manually if required');
      }

    },
    saveParameters() {
      const params = {
        initialParams: this.initialParams,
        basicParams: this.basicParams,
        advancedParams: this.advancedParams,
        keySettings: this.keySettings,
        aimHeight: this.aimHeight,
        mouseDriver: this.mouseDriver,
        kmboxNetParams: this.kmboxNetParams

      };
      localStorage.setItem('savedParameters', JSON.stringify(params));
      console.log(JSON.stringify(params));
      alert(this.language === 'zh' ? '参数已保存' : 'Parameters saved');
    },
    loadParameters() {
      const savedParams = localStorage.getItem('savedParameters');
      if (savedParams) {
        const params = JSON.parse(savedParams);
        this.initialParams = params.initialParams;
        this.basicParams = params.basicParams;
        this.advancedParams = params.advancedParams;
        this.keySettings = params.keySettings;
        this.aimHeight = params.aimHeight;
        this.mouseDriver = params.mouseDriver;
        this.kmboxNetParams = params.kmboxNetParams;
      }
    },
    async sendData() {
      const params = {
        initialParams: this.initialParams,
        basicParams: this.basicParams,
        advancedParams: this.advancedParams,
        keySettings: this.keySettings,
        aimHeight: this.aimHeight,
        mouseDriver: this.mouseDriver,
        kmboxNetParams: this.kmboxNetParams,
        language: this.language
      };
      
      
    try {
      const data = JSON.stringify(params);
      const response = await axios.post('/api/data', data);
      console.log('Response:', response.data);
      //alert(this.language === 'zh' ? '参数已更新' : 'Parameters Updated');
    } catch (error) {
      console.error('Error:', error);
    }
  },
    async fetchData() {
      try {
        const response = await axios.get('/api/data');
        
        this.receivedData = response.data; // 更新接收到的数据
        console.log(this.receivedData)
        if (this.receivedData){
          if(this.receivedData.includes("ERROR_0") ||this.receivedData.includes("INFO_0")){
            this.isInferencing = false;
          }
          this.updateDebugMessages(); // 更新调试信息
        }
        
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },

    updateDebugMessages() {
      const timestamp = new Date().toLocaleTimeString();
      const message = `${this.language === 'zh' ? '||' : '||'} ${this.receivedData} (${timestamp})`;
      if (this.debugMessages.length >= 50) {
        this.debugMessages = []; // 清空调试信息
      }
      this.debugMessages.push(message);
    }
  },
  computed: {
    formattedKeySettings() {
      return {
        forward: `${this.keySettings.forward.key} (${this.keySettings.forward.vk})`,
        backward: `${this.keySettings.backward.key} (${this.keySettings.backward.vk})`,
        left: `${this.keySettings.left.key} (${this.keySettings.left.vk})`,
        right: `${this.keySettings.right.key} (${this.keySettings.right.vk})`
      };
    },
    aimHeightPercentage() {
      return (this.aimHeight / 100).toFixed(2);
    }
  },
  watch: {
    initialParams: {
      handler: 'sendData',
      deep: true
    },
    basicParams: {
      handler: 'sendData',
      deep: true
    },
    advancedParams: {
      handler: 'sendData',
      deep: true
    },
    keySettings: {
      handler: 'sendData',
      deep: true
    },
    language: 'sendData',
    aimHeight: 'sendData',
    mouseDriver: 'sendData',
    kmboxNetParams: {
      handler: 'sendData',
      deep: true
    }
  }
}
</script>

<style scoped>
.fixed-screen {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
body {
  @apply bg-gray-900 text-white font-sans;
}
.bg-custom-green {
  background-color: #28a745;
}
.bg-custom-red {
  background-color: #dc3545;
}
