    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.breathing-animation {
  animation: breathing 3s ease-in-out infinite;
}
@keyframes breathing {
  0%, 100% {
    color: #ffffff;
  }
  50% {
    color: #ff6347;
  }
}
.bg-custom-blue-light {
  background-color: #1e90ff;
}
.select-none {
  user-select: none;
  -webkit-user-drag: none;
}
.w-12 {
  width: 3rem;
}
.text-right {
  text-align: right;
}
.toggle-switch {
  width: 50px;
  height: 25px;
  background: #ccc;
  border-radius: 25px;
  position: relative;
  cursor: pointer;
  transition: background 0.3s;
}
.toggle-switch-thumb {
  width: 23px;
  height: 23px;
  background: white;
  border-radius: 50%;
  position: absolute;
  top: 1px;
  transition: left 0.3s;
}
.toggle-switch-thumb-on {
  left: 2px;
  background: #28a745;
}
.toggle-switch-thumb-off {
  left: 25px;
  background: #dc3545;
}
input:disabled, select:disabled {
  cursor: not-allowed; /* change cursor to indicate disabled state */
  color: #535658;
}
</style>
